.skills {
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  padding: 5rem 0;
  margin-bottom: 0;
  position: relative;
  overflow: hidden;
}

.skills::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grain" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="50" cy="50" r="1" fill="%23ffffff" opacity="0.1"/></pattern></defs><rect width="100" height="100" fill="url(%23grain)"/></svg>');
  opacity: 0.3;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.section-title {
  text-align: center;
  margin-bottom: 3rem;
  position: relative;
}

.title-text {
  font-size: 3.5rem;
  font-weight: 800;
  color: #1a202c;
  display: block;
  margin-bottom: 1rem;
  letter-spacing: 2px;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  animation: fadeInUp 1s ease-out;
}

.title-underline {
  width: 120px;
  height: 4px;
  background: linear-gradient(90deg, #ff6b6b, #4ecdc4, #45b7d1, #96ceb4);
  margin: 0 auto;
  border-radius: 2px;
  position: relative;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  animation: slideIn 1.2s ease-out;
}

.title-underline::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  background: linear-gradient(45deg, #ffffff, #f8f9fa);
  border-radius: 50%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  animation: pulse 2s ease-in-out infinite;
}

/* View Controls */
.view-controls {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
}

.view-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 2rem;
  background: rgba(255, 255, 255, 0.9);
  border: 2px solid transparent;
  border-radius: 50px;
  font-size: 1rem;
  font-weight: 600;
  color: #333;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.view-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.view-btn.active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-color: rgba(255, 255, 255, 0.3);
}

.btn-icon {
  font-size: 1.2rem;
}

/* Skills Grid Styles */
.flowchart-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto auto;
  gap: 20px;
  padding: 20px;
  background: #ffffff;
  border: 1px solid #cccccc;
  max-width: 1200px;
  margin: 0 auto;
}

.flowchart-node {
  background: #ffffff;
  border: 1px solid #cccccc;
  padding: 20px;
  min-height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.flowchart-node:hover {
  background: #ffffff;
  border: 1px solid #cccccc;
}

.main-node {
  grid-column: 1 / -1;
  text-align: center;
  background: #667eea;
  color: white;
  margin-bottom: 20px;
}

.main-node .node-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.main-node .node-icon {
  font-size: 2rem;
}

.main-node .node-title {
  font-size: 1.5rem;
  font-weight: bold;
}

.main-node .node-subtitle {
  font-size: 1rem;
}

.category-node {
  border-left: 4px solid var(--color);
}

.node-content {
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex: 1;
}

.node-icon {
  font-size: 1.5rem;
  text-align: center;
}

.node-title {
  font-size: 1.2rem;
  font-weight: bold;
  color: #333;
  text-align: center;
}


.node-connections {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: flex-start;
}

.skill-connection {
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 5px;
  background: #f0f0f0;
  margin-bottom: 5px;
}

.connection-dot {
  width: 6px;
  height: 6px;
  background: var(--color);
  border-radius: 50%;
}

.skill-name {
  font-size: 0.9rem;
  color: #333;
}

.more-skills {
  text-align: center;
  font-size: 0.8rem;
  color: #999;
  font-style: italic;
  padding: 0.5rem;
  background: rgba(0, 0, 0, 0.05);
  border-radius: 10px;
}

/* JSON Viewer Styles */
.json-viewer {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  overflow: hidden;
}

.json-container {
  background: #1e1e1e;
  border-radius: 15px;
  overflow: hidden;
  border: 1px solid #404040;
}

.json-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.5rem;
  background: #2d2d2d;
  border-bottom: 1px solid #404040;
}

.json-icon {
  font-size: 1.5rem;
}

.json-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: #fff;
  font-family: 'Monaco', 'Menlo', monospace;
}

.json-status {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-left: auto;
  font-size: 0.9rem;
  color: #4CAF50;
}

.status-dot {
  width: 8px;
  height: 8px;
  background: #4CAF50;
  border-radius: 50%;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

.json-content {
  padding: 1.5rem;
  max-height: 600px;
  overflow-y: auto;
}

.json-code {
  font-family: 'Monaco', 'Menlo', monospace;
  font-size: 0.9rem;
  line-height: 1.6;
  color: #d4d4d4;
  margin: 0;
  white-space: pre-wrap;
}

.json-category {
  margin: 1rem 0;
}

.json-skills {
  margin-left: 2rem;
}


/* Network Styles */
.network-container {
  position: relative;
  width: 100%;
  max-width: 500px;
  height: 400px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  overflow: visible;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px;
  margin: 2rem auto;
}

/* Iconos individuales */
.skill-icon-node {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-120px) rotate(calc(-1 * var(--angle)));
  animation: float 3s ease-in-out infinite;
  animation-delay: calc(var(--angle) * 0.1s);
  z-index: 5;
}

.icon-content {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 50%;
  padding: 10px;
  border: 2px solid var(--color);
  text-align: center;
  min-width: 60px;
  min-height: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease;
  cursor: pointer;
}

.icon-content:hover {
  transform: scale(1.1) translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.25);
  border-color: #ff6b6b;
}

.skill-icon {
  font-size: 1.8rem;
  margin-bottom: 0.2rem;
  display: block;
  color: var(--color);
}

.skill-icon svg {
  width: 1.8rem;
  height: 1.8rem;
  fill: currentColor;
}

.skill-name {
  font-size: 0.6rem;
  font-weight: 600;
  color: #2c3e50;
  text-align: center;
  line-height: 1.1;
}

.network-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
}

.center-node {
  width: 80px;
  height: 80px;
  background: linear-gradient(135deg, #ff6b6b 0%, #ffa726 100%);
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 20px rgba(255, 107, 107, 0.5);
  animation: pulse 2s infinite, rotate 10s linear infinite;
  cursor: pointer;
  transition: all 0.3s ease;
}

.center-node:hover {
  transform: scale(1.1);
  box-shadow: 0 0 40px rgba(255, 107, 107, 0.8);
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

@keyframes rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.center-icon {
  font-size: 1.8rem;
  margin-bottom: 0.3rem;
}

.center-title {
  font-size: 0.8rem;
  font-weight: 700;
  text-align: center;
}

.network-node {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-300px) rotate(calc(-1 * var(--angle)));
  animation: float 3s ease-in-out infinite;
  animation-delay: calc(var(--angle) * 0.1s);
  z-index: 5;
}

/* Z-index específico para cada tarjeta basado en su posición */
.network-node:nth-child(7) { z-index: 8; } /* Frontend - primera tarjeta */
.network-node:nth-child(8) { z-index: 6; } /* Backend */
.network-node:nth-child(9) { z-index: 5; } /* Herramientas */
.network-node:nth-child(10) { z-index: 4; } /* Protocolos */
.network-node:nth-child(11) { z-index: 7; } /* Metodologías */
.network-node:nth-child(12) { z-index: 3; } /* Idiomas - última tarjeta */

/* Posicionamiento específico para Frontend (más abajo) */
.frontend-node {
  transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-280px) translateX(-50px) rotate(calc(-1 * var(--angle))) !important;
}

/* Posicionamiento específico para Idiomas (separado pero conectado al centro) */
.idiomas-node {
  transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-380px) translateX(-90px) rotate(calc(-1 * var(--angle))) !important;
}


.network-node .node-content {
  background: #ffffff;
  border-radius: 15px;
  padding: 1.5rem;
  border: 2px solid var(--color);
  text-align: center;
  min-width: 180px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  cursor: pointer;
  color: #2c3e50;
}

.network-node .node-content:hover {
  transform: scale(1.1) translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.25);
  border-color: #ff6b6b;
}

@keyframes float {
  0%, 100% { 
    transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-300px) rotate(calc(-1 * var(--angle))) translateY(0px); 
  }
  50% { 
    transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-300px) rotate(calc(-1 * var(--angle))) translateY(-10px); 
  }
}

/* Animación específica para tarjetas con posiciones personalizadas */
.frontend-node {
  animation: float-frontend 3s ease-in-out infinite !important;
  animation-delay: calc(var(--angle) * 0.1s) !important;
}

.idiomas-node {
  animation: float-idiomas 3s ease-in-out infinite !important;
  animation-delay: calc(var(--angle) * 0.1s) !important;
}

@keyframes float-frontend {
  0%, 100% { 
    transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-280px) translateX(-50px) rotate(calc(-1 * var(--angle))) translateY(0px);
  }
  50% { 
    transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-280px) translateX(-50px) rotate(calc(-1 * var(--angle))) translateY(-10px);
  }
}

@keyframes float-idiomas {
  0%, 100% { 
    transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-380px) translateX(-90px) rotate(calc(-1 * var(--angle))) translateY(0px);
  }
  50% { 
    transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-380px) translateX(-90px) rotate(calc(-1 * var(--angle))) translateY(-10px);
  }
}

.connection-line {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 2px;
  height: 120px;
  background: linear-gradient(to bottom, var(--color), transparent);
  transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-60px);
  animation: pulse-line 2s ease-in-out infinite;
  animation-delay: calc(var(--angle) * 0.1s);
  opacity: 0.6;
  z-index: 1;
}

/* Líneas de conexión específicas para Frontend e Idiomas */
.connection-line.frontend-line {
  height: 280px;
  transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-140px) translateX(-25px);
}

/* Línea de conexión personalizada para Idiomas que va al centro */
.connection-line.idiomas-line {
  height: 380px;
  width: 3px;
  background: linear-gradient(to bottom, #ff8c00, transparent) !important;
  transform: translate(-50%, -50%) rotate(300deg) translateY(-190px);
  opacity: 0.6;
  z-index: 2;
  animation: pulse-line 2s ease-in-out infinite, idiomas-connect 3s ease-in-out infinite;
}

@keyframes idiomas-connect {
  0%, 100% { 
    transform: translate(-50%, -50%) rotate(300deg) translateY(-190px);
    opacity: 0.4;
  }
  50% { 
    transform: translate(-50%, -50%) rotate(300deg) translateY(-185px);
    opacity: 0.8;
  }
}

/* Punto animado específico para la línea de Idiomas - igual que los otros */
.connection-line.idiomas-line::before {
  content: '';
  position: absolute;
  top: 0;
  left: -1px;
  width: 4px;
  height: 20px;
  background: #ff8c00;
  border-radius: 2px;
  animation: move-dot 3s linear infinite;
  animation-delay: calc(300deg * 0.1s);
}

.connection-line::before {
  content: '';
  position: absolute;
  top: 0;
  left: -1px;
  width: 4px;
  height: 20px;
  background: var(--color);
  border-radius: 2px;
  animation: move-dot 3s linear infinite;
  animation-delay: calc(var(--angle) * 0.1s);
}

@keyframes pulse-line {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 0.8; }
}

@keyframes move-dot {
  0% { transform: translateY(0); }
  100% { transform: translateY(230px); }
}

.network-node .node-icon {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.network-node .node-title {
  font-size: 1.1rem;
  font-weight: 700;
  color: #2c3e50;
  margin-bottom: 1rem;
}

.node-skills {
  display: flex;
  flex-wrap: wrap;
  gap: 0.3rem;
  justify-content: center;
}

.skill-tag {
  background: var(--color);
  color: white;
  padding: 0.2rem 0.6rem;
  border-radius: 10px;
  font-size: 0.7rem;
  font-weight: 500;
}

/* Estilo específico para la tarjeta de Idiomas */
.network-node:nth-child(12) .skill-tag {
  background: #ff8c00;
  color: white;
  font-weight: 600;
}

/* Modal styles removed */

/* Animations removed for cleaner experience */

/* Responsive Design */
@media (max-width: 1024px) {
  .flowchart-container {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
  
  .network-container {
    height: 500px;
  }
  
  .network-node .node-content {
    min-width: 150px;
    padding: 1rem;
  }
}

@media (max-width: 1200px) {
  .title-text {
    font-size: 3rem;
  }
  
  .flowchart-container {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
  
  .network-container {
    min-height: 500px;
    padding: 120px 40px;
  }
  
  .skill-icon-node {
    transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-120px) rotate(calc(-1 * var(--angle)));
  }
  
  .connection-line {
    height: 120px;
    transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-60px);
  }
}

@media (max-width: 768px) {
  .skills {
    padding: 3rem 0;
  }
  
  .title-text {
    font-size: 2.5rem;
  }
  
  .view-controls {
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
  }
  
  .view-btn {
    width: 100%;
    max-width: 300px;
    justify-content: center;
    padding: 0.8rem 1rem;
    font-size: 0.9rem;
  }
  
  .flowchart-container {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(6, 1fr);
    padding: 1rem;
  }
  
  .network-container {
    min-height: 400px;
    padding: 100px 30px;
  }
  
  .center-node {
    width: 60px;
    height: 60px;
  }
  
  .center-icon {
    font-size: 1.5rem;
  }
  
  .center-title {
    font-size: 0.7rem;
  }
  
  .skill-icon-node {
    transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-80px) rotate(calc(-1 * var(--angle)));
  }
  
  .connection-line {
    height: 80px;
    transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-40px);
  }
  
  .icon-content {
    min-width: 50px;
    min-height: 50px;
    padding: 8px;
  }
  
  .skill-icon {
    font-size: 1.5rem;
  }
  
  .skill-icon svg {
    width: 1.5rem;
    height: 1.5rem;
  }
  
  .skill-name {
    font-size: 0.5rem;
    line-height: 1.1;
  }
}

@media (max-width: 480px) {
  .skills {
    padding: 2rem 0;
  }
  
  .title-text {
    font-size: 2rem;
  }
  
  .view-controls {
    gap: 0.6rem;
  }
  
  .view-btn {
    padding: 0.7rem 0.8rem;
    font-size: 0.8rem;
  }
  
  .network-container {
    min-height: 350px;
    padding: 80px 20px;
  }
  
  .center-node {
    width: 50px;
    height: 50px;
  }
  
  .center-icon {
    font-size: 1.2rem;
  }
  
  .center-title {
    font-size: 0.6rem;
  }
  
  .skill-icon-node {
    transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-60px) rotate(calc(-1 * var(--angle)));
  }
  
  .connection-line {
    height: 60px;
    transform: translate(-50%, -50%) rotate(var(--angle)) translateY(-30px);
  }
  
  .icon-content {
    min-width: 40px;
    min-height: 40px;
    padding: 6px;
  }
  
  .skill-icon {
    font-size: 1.2rem;
  }
  
  .skill-icon svg {
    width: 1.2rem;
    height: 1.2rem;
  }
  
  .skill-name {
    font-size: 0.4rem;
    line-height: 1;
  }
  
  .json-content {
    padding: 1rem;
  }
  
  .json-code {
    font-size: 0.8rem;
  }
  
  .modal-content {
    padding: 1.5rem;
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideIn {
  0% {
    width: 0;
    opacity: 0;
  }
  100% {
    width: 120px;
    opacity: 1;
  }
}

@keyframes pulse {
  0%, 100% {
    transform: translate(-50%, -50%) scale(1);
  }
  50% {
    transform: translate(-50%, -50%) scale(1.1);
  }
}